// ** Third Party Components
import { MoreHorizontal } from 'react-feather'
import { activeOrganization } from '@src/helper/sassHelper'
import { useEffect, useState } from 'react'
const activeOrg = activeOrganization()
const VerticalNavMenuSectionHeader = ({ item }) => {

  const [orgName, setorgName] = useState('')

  useEffect(() => {
    setorgName(activeOrg?.name)
  }, [])

  return (
    <li className='navigation-header'>
      <span>{item.custom ? orgName : item.header}</span>
      <MoreHorizontal className='feather-more-horizontal' />
    </li>
  )
}

export default VerticalNavMenuSectionHeader
