// ** React Imports
import { Link, useNavigate } from 'react-router-dom'
import { useContext, useEffect, useState } from 'react'
import { AbilityContext } from '@src/utility/context/Can'
// ** Custom Components
import Avatar from '@components/avatar'

// ** Store & Actions
import { Can } from '@src/utility/context/Can.js'
import { useDispatch, useSelector } from 'react-redux'
import { handleLogout } from '@store/authentication'

// ** Third Party Components
import { Power, Settings } from 'react-feather'
// ** Reactstrap Imports
import { DropdownItem, DropdownMenu, DropdownToggle, UncontrolledDropdown } from 'reactstrap'
// ** Default Avatar Image
import defaultAvatar from '@src/assets/images/avatars/avatar-blank.png'
import { listOrg } from '@src/views/pages/authentication/register-multi-steps/store/index'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const MySwal = withReactContent(Swal)

const ListOrg = ({ organizations, activeOrgId }) => {
    return (
        organizations.map((obj, k) => {
            return (<p key={k}
                className={`cursor-pointer px-1 pb-0 pt-0 ${obj.id === activeOrgId ? 'text-success' : ''}`}                > {obj.name} </p>)
        })
    )
}

const UserDropdown = () => {
    // ** Store Vars
    const dispatch = useDispatch()
    const ability = useContext(AbilityContext)
    const navigate = useNavigate()
    //const store = useSelector(state => state.auth)
    const regstore = useSelector(state => state.register)
    const [activeOrgId, setActiveOrgId] = useState(null)
    const [activeOrg, setActiveOrg] = useState({})
    const userAvatar = defaultAvatar

    useEffect(async () => {
        const userdata = JSON.parse(localStorage.getItem('userData'))
        if (userdata) {
            await dispatch(listOrg({ userId: userdata?.id, userType: userdata?.usertype }))
        } else {
            navigate('/login')
        }
    }, [])

    useEffect(async () => {
        if (regstore.organizationList.length > 0) {
            setActiveOrgId(regstore.organizationList[0])
            setActiveOrg(regstore.organizationList[0])
            const abilitytemp = []
            const data = regstore.organizationList[0]?.permissions.split(',')
            data?.forEach(obj => {
                const action = obj.substring(obj.lastIndexOf("-") + 1)
                const resource = obj.replace(obj.substring(obj.lastIndexOf("-")), '')
                const opt = { action, subject: resource }
                abilitytemp.push(opt)
            })
            localStorage.setItem('ability', JSON.stringify(abilitytemp))
            await ability.update(abilitytemp)

            localStorage.setItem('activeOrganization', JSON.stringify(regstore.organizationList[0]))
            if (!localStorage.getItem('clientId')) {
                localStorage.setItem('clientId', regstore.organizationList[0]?.clientid)
                localStorage.setItem('clientName', regstore.organizationList[0]?.clientname)
            }
        }
    }, [regstore.organizationList])


    const getOrgDetails = () => {

    }

    useEffect(() => {
        if (activeOrgId) {
            getOrgDetails()
        }
    }, [activeOrgId])

    const logoutFun = () => {

        const task = localStorage.getItem('currentTask')
        const breakLog = localStorage.getItem('breakLogin')

        if (task || breakLog) {

            return MySwal.fire({
                title: 'Warning!',
                text: ' You have Running Task or Running Break,Please Stop timer to Logout!',
                icon: 'warning',
                customClass: {
                    confirmButton: 'btn btn-primary'
                },
                buttonsStyling: false
            })

        } else {
            dispatch(handleLogout())
            navigate('/login')
        }
    }

    return (
        <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
            <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
                <div className='user-nav d-sm-flex d-none'>
                    <span className='user-name fw-bold'>{activeOrg?.orgusername?.split(' ')[0]}</span>
                    <span className='user-status'>{activeOrg.rolename}</span>
                </div>

                <Avatar img={userAvatar} imgHeight='40' imgWidth='40' status='online' />

            </DropdownToggle>
            <DropdownMenu end style={{ minWidth: '26rem' }}>

                <Can I={'read'} a={'setting'}>
                    <DropdownItem tag={Link} to='/account-settings'>
                        <Settings size={14} className='me-75' />
                        <span className='align-middle'>Setting</span>
                    </DropdownItem>
                </Can>

                <DropdownItem tag={Link} to='/login' onClick={() => logoutFun()}>
                    <Power size={14} className='me-75' />
                    <span className='align-middle'>Logout</span>
                </DropdownItem>

                <DropdownItem divider />
                <h5 className='px-1'>Organizations</h5>
                {regstore.organizationList.length > 0 &&
                    <ListOrg organizations={regstore.organizationList} activeOrgId={activeOrgId} />
                }
            </DropdownMenu>
        </UncontrolledDropdown>
    )
}

export default UserDropdown
