// ** Redux Imports
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

// ** Axios Imports
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()

export const getData = createAsyncThunk('appRole/getData', async params => {
  const response = await axios.post(`/roles/list`, params)
  return {
    params,
    data: response.data.roles
  }
})
export const addRole = createAsyncThunk('appRole/addRole', async (data, { dispatch, getState }) => {

  const res = await axios.post(`/roles/create`, data)
  await dispatch(getData(getState().role.params))
  return res.data.role

})

export const updateRole = createAsyncThunk('appRole/updateRole', async (data, { dispatch, getState }) => {
  await axios.post(`/roles/update`, data)
  await dispatch(getData(getState().role.params))
  return []
})

export const addRolePermissions = createAsyncThunk('appRole/addRolePermissions', async (data, { }) => {

  await axios.post(`/rolepermissions/create`, data)

  return []

})

export const updateRolePermissions = createAsyncThunk('appRole/updateRolePermissions', async (data, { }) => {
  await axios.post(`/rolepermissions/update`, data)
  return []
})

export const deleteRole = createAsyncThunk('appRole/deleteRole', async (id, { dispatch, getState }) => {
  await axios.post('/roles/delete', { id, updatedBy: userId })
  await dispatch(getData(getState().role.params))
  return id
})

export const appRolesSlice = createSlice({
  name: 'appRole',
  initialState: {
    data: [],
    total: 1,
    params: {},
    allData: []
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(getData.fulfilled, (state, action) => {
        state.data = action.payload.data
        state.params = action.payload.params
      })

  }
})

export default appRolesSlice.reducer
