// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Custom Components
import NavbarUser from './NavbarUser'
import NavbarBookmarks from './NavbarBookmarks'
import { useDispatch, useSelector } from 'react-redux'
import { getClient } from '@src/views/apps/client/store/index.js'

const ThemeNavbar = props => {
  // ** Props
  const { skin, setSkin, setMenuVisibility } = props
  const dispatch = useDispatch()
  const orgstore = useSelector(state => state.organizationmain)
  const [clientName, setClientName] = useState('')
  const [activeOrga, setActiveOrga] = useState(null)

  const getClientDetails = async (id) => {
    const res = await dispatch(getClient(id))
    setClientName(res.payload?.name)
    localStorage.setItem('clientName', res.payload?.name)
  }

  useEffect(async () => {
    if (!activeOrga?.clientid) {
      const clientId = localStorage.getItem('clientId')
      if (clientId !== 'null') {
        await getClientDetails(clientId)
      }
    } else {
      setClientName(activeOrga?.clientname)
      localStorage.setItem('clientName', activeOrga?.clientname)
    }

  }, [activeOrga])

  useEffect(() => {
    const org = localStorage.getItem('activeOrganization')
    setActiveOrga(org)

  }, [orgstore.localActiveOrg])

  const renderOrgName = () => {
    return (<h4 className='text-primary mb-0'>{(clientName?.length > 20 ? `${clientName?.substr(0, 20)}...` : clientName)}</h4>)
  }

  return (
    <Fragment>
      <div className='bookmark-wrapper d-flex align-items-center justify-content-between'>
        <NavbarBookmarks setMenuVisibility={setMenuVisibility} />
      </div>
      <div className='bookmark-wrapper d-flex align-items-center'>
        {renderOrgName()}
      </div>
      <NavbarUser skin={skin} setSkin={setSkin} />
    </Fragment>
  )
}

export default ThemeNavbar
